<template>
    <div class="container mt-5">
        <div class="row">
            <div class="col-md-12">
                <div class="card border-0 rounded shadow">
                    <div class="card-body">
                        <h4>TRANSAKSI AKTIVASI</h4>
                        <hr>
                        <form @submit.prevent="store">
                            <div class="form-group mb-3"> <label class="form-label">Pegawai</label> 
                                <select
                                    class="form-select"
                                    aria-label="Default select example"
                                    v-model="transaksi_aktivasi.id_pegawai"
                                    >
                                    <option
                                        v-for="(pegawai, id_pegawai) in pegawais"
                                        :key="id_pegawai"
                                        :value="pegawai.id_pegawai"
                                        >
                                        {{ pegawai.nama }}  -  {{ pegawai.role }}
                                    </option>
                                </select>
                                <div v-if="validation.id_pegawai" class="mt-2 alert alert-danger"> {{
                                    validation.id_pegawai[0] }} </div>
                            </div>
                            <div class="form-group mb-3"> <label class="form-label">Member</label> 
                                <select
                                    class="form-select"
                                    aria-label="Default select example"
                                    v-model="transaksi_aktivasi.id_member"
                                    >
                                    <option
                                        v-for="(member, id_member) in members"
                                        :key="id_member"
                                        :value="member.id_member">
                                        {{member.id_member}}  -  {{ member.nama }} 
                                    </option>
                                </select>
                                <div v-if="validation.id_member" class="mt-2 alert alert-danger"> {{
                                    validation.id_member[0] }} </div>
                            </div>
                            <button type="submit" class="btn btn-primary">SIMPAN</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { reactive, ref, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import axios from 'axios'

export default {
    setup() {
        //state departemen 
        const transaksi_aktivasi = reactive({ 
            id_pegawai : '',
            id_member : '',
        })
        //state validation 
        const validation = ref([])
        //vue router 
        const router = useRouter()

        let pegawais = ref([])

        let members = ref([])

        onMounted(() => {
            //get API from Laravel Backend 
            axios.get('https://api-gofit.200710947.com/api/pegawai') 
            .then(response => { 
                //assign state posts with response data 
                pegawais.value = response.data.data 
            }).catch(error => {
                 console.log(error.response.data) 
            }) 
        }) 

        onMounted(() => {
            //get API from Laravel Backend 
            axios.get('https://api-gofit.200710947.com/api/member') 
            .then(response => { 
                //assign state posts with response data 
                members.value = response.data.data 
            }).catch(error => {
                 console.log(error.response.data) 
            }) 
        }) 

        //method store 
        function store() { 
            let id_pegawai = transaksi_aktivasi.id_pegawai
            let id_member = transaksi_aktivasi.id_member
            axios.post('https://api-gofit.200710947.com/api/transaksi_aktivasi', { 
                id_pegawai : id_pegawai,
                id_member : id_member,
            }).then(() => {
                 //redirect ke post index 
                 router.push({ name: 'transaksi_aktivasi.index' }) }).catch(error => { 
                    //assign state validation with error 
                    validation.value = error.response.data 
                }) 
            

            axios.post('https://api-gofit.200710947.com/api/member/aktivasi/'+ transaksi_aktivasi.id_member)
            .then(response => { 
                //assign state posts with response data 
                members.value = response.data.data 
            }).catch(error => {
                 console.log(error.response.data) 
            }) 

            }
            return { 
                transaksi_aktivasi, 
                pegawais,
                members,
                validation, 
                router, 
                store 
         }
    }
}
</script>

